<template>
  <div>
    <b-card v-if="summary_loaded" title="Payment" sub-title="Summary">
      <b-card-text>Name: {{summary.offer.name}}</b-card-text>
      <b-card-text>Amount:  Rs {{summary.rzr_data.amount/100}}/-</b-card-text>

      <b-button variant="success" id="rzp-button1" @click="payClick">Confirm Pay</b-button>
      &nbsp;
      <router-link to="/home" tag="b-button">Go back</router-link>
    </b-card>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { router } from "../../router";
export default {
  name: "PaymentSummary",
  computed: {
    ...mapState("payment", ["summary"])
  },
//   data: () => {
//     return {
//       MOHAHAHAHAH: process.env.VUE_APP_PMNT_GATEWAY_KEY,
//     };
//   },
  data: () => {
    return {
      summary_loaded: false,
     };
  },
  methods: {
    ...mapActions("payment", ["prePayment", "postPayment"]),
    payClick() {
      let vm = this;
      var options = {
        key: process.env.VUE_APP_PMNT_GATEWAY_KEY, 
        amount: this.summary.rzr_data.amount,
        currency: "INR",
        name: "Splendre EyeMag Pvt Ltd",
        description: "Engineered ad delivery",
        image: "https://splendre.com/images/logo.png",
        order_id: this.summary.rzr_data.id, 
        handler: function(response) {
         // Send this to server for  saving and verification
         vm.postPayment(response);
         //alert(response.razorpay_payment_id);
        },
        prefill: {
          name: "",
          email: ""
        },
        notes: {
          address: ""
        },
        theme: {
          color: "#8bc53d"
        }
      };
      var rzp1 = new Razorpay(options);
      rzp1.open();
    }
  },
  mounted: function() {
    this.prePayment({ offer_id: this.$route.params.id })
    .then(()=>{
      this.summary_loaded=true
    })
  }
};
</script>
